<template>
  <div>
    <a-form>
      <a-row :gutter="20">
        <a-col :span="12">
          <a-form-item label="">
            <a-input v-model="model.title"></a-input>
          </a-form-item>
          <a-form-item label="">
            <a-select v-model="model.type" placeholder="">
              <a-select-option v-for="item in categories" :key="item.code" :value="item.code">{{item.name}}
              </a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="">
            <a-textarea v-model="model.desc" :autosize="{minRows:2,maxRows:8}"></a-textarea>
          </a-form-item>
          <a-form-item label="">
            <a-input v-model="model.position"></a-input>
          </a-form-item>
          <a-form-item label="">
            <a-input v-model="model.location"></a-input>
            <a href="https://lbs.amap.com/console/show/picker" target="_blank"></a>
          </a-form-item>
          <a-form-item label="">
            <upload v-model="model.images" :size="5"></upload>
          </a-form-item>
        </a-col>
      </a-row>
      <a-divider orientation="left"></a-divider>
      <a-row :gutter="20">
        <a-col :span="12">
          <a-form-item label="">
            <a-input v-model="model.audio.name"></a-input>
          </a-form-item>
        </a-col>
      </a-row>
      <a-form-item label="">
        <upload v-model="model.audio.src" listType="text" accept="audio/*"></upload>
      </a-form-item>
      <a-form-item label="">
        <upload v-model="model.audio.poster"></upload>
      </a-form-item>
      <a-button type='primary' :loading="loading" @click="submit">
        
      </a-button>
    </a-form>
  </div>
</template>
<style lang="scss" scoped>
  @import './index.scss';
</style>
<script>
  import upload from '../../../components/upload'
  import Sites from '../../../model/sites'

  export default {
    data () {
      return {
        model: {
          title: '',
          type: undefined,
          desc: '',
          position: '',
          location: '',
          audio: {
            name: '',
            poster: '',
            src: ''
          },
          images: []
        },
        categories: [{
          name: '',
          code: 'scenic'
        }, {
          name: '',
          code: 'dept'
        }, {
          name: '',
          code: 'building'
        }, {
          name: '',
          code: 'dormitory'
        }, {
          name: '',
          code: 'diningroom'
        }, {
          name: '',
          code: 'library'
        }, {
          name: '',
          code: 'site'
        }, {
          name: '',
          code: 'gate'
        }, {
          name: 'ATM',
          code: 'atm'
        }, {
          name: '',
          code: 'toilet'
        }, {
          name: '',
          code: 'service'
        }
        ],
        loading: false,
        siteId: undefined
      }
    },
    components: {upload},
    created () {
      this.siteId = this.$route.params.id
      if (!this.siteId) return
      this.loadEditData(this.siteId)
    },
    mounted () {
    },
    beforeDestroy () {
    },
    methods: {
      async loadEditData (id) {
        this.model = await Sites.show(id)
        console.log(this.model)
      },
      async submit () {
        try {
          this.isUpdate ? await Sites.edit(this.siteId, this.model) : await Sites.create(this.model)
          this.$message.success('')
          this.back()
        } catch (err) {
          console.error(err)
        }
      },
      back () {
        setTimeout(() => {
          this.$router.replace('/sites')
        }, 800)
      }
    },
    computed: {
      isUpdate () {
        return this.siteId !== undefined
      }
    },
    watch: {}
  }
</script>
